/*! ==============================================================================
1. Resets
=============================================================================== */
/* ============================================================================ */
/* Global Selector                                                              */
/* ============================================================================ */
* {
  box-sizing: border-box;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

/* ============================================================================ */
/* HTML                                                                         */
/* ============================================================================ */
html {
  color: var(--color-neutral-9);
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  overflow: hidden;
  overflow-x: hidden;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
}
html {
  -servicestudio-overflow: hidden;
}

/* ============================================================================ */
/* Body                                                                         */
/* ============================================================================ */
body {
  background-color: var(--color-background-body);
  direction: ltr;
  font-size: var(--font-size-s);
  line-height: 1.5;
  margin: 0;
}

/* ============================================================================ */
/* Focus state                                                                  */
/* ============================================================================ */
:focus {
  outline: 0;
}

::-moz-focus-inner {
  border: 0;
}

.has-accessible-features :focus {
  box-shadow: 0 0 0 3px var(--color-focus-outer);
}

/* ============================================================================ */
/* Abbreviation                                                                 */
/* ============================================================================ */
abbr[title] {
  border-bottom: 0;
  text-decoration: underline;
}

/* ============================================================================ */
/* Style the cursor when a div elem is clickable                                */
/* ============================================================================ */
div[onclick] {
  cursor: pointer;
}

/* ============================================================================ */
/* Label                                                                        */
/* ============================================================================ */
label {
  color: var(--color-neutral-8);
}

[data-label].mandatory:after {
  color: var(--color-error);
  margin-left: var(--space-xs);
}

/* ============================================================================ */
/* Inputs Styles                                                                */
/* ============================================================================ */
input,
textarea,
select,
button {
  appearance: none;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.25;
  margin: 0;
}

[type=checkbox],
[type=radio] {
  border: 0;
  padding: 0;
}

[type=number]::-webkit-inner-spin-button, [type=number]::-webkit-outer-spin-button {
  height: auto;
}

[type=search] {
  appearance: textfield;
  outline-offset: -2px;
}
[type=search]::-webkit-search-cancel-button, [type=search]::-webkit-search-decoration {
  appearance: none;
}

input[type=time], input[type=date], input[type=datetime], input[type=datetime-local], input[type=time], input[type=time]:empty {
  display: inline-flex;
}

::-webkit-file-upload-button {
  appearance: button;
}

select::-ms-expand {
  display: none;
}

input::-ms-clear {
  display: none;
}

/* ============================================================================ */
/* Button                                                                       */
/* ============================================================================ */
button.OSFillParent {
  display: inline-flex;
}

/* ============================================================================ */
/* FocusTrap                                                                    */
/* ============================================================================ */
.focus-trap-top {
  top: 0;
}
.focus-trap-bottom {
  bottom: 0;
}

/*! ==============================================================================
2. HTML Elements
=============================================================================== */
/*! 2.1. Links */
/* HTML Elements - Links */
a {
  text-decoration: none;
  -webkit-text-decoration-skip: objects;
}
a, a:visited {
  color: var(--color-primary);
  transition: all 180ms linear;
}
a:hover, a:focus {
  color: var(--color-primary-hover);
  text-decoration: underline;
}
a[disabled] {
  color: var(--color-neutral-6);
}

.phone a:hover, .phone a:focus,
.tablet a:hover,
.tablet a:focus {
  text-decoration: none;
}

.has-accessible-features a:focus {
  background-color: var(--color-focus-outer);
  box-shadow: 0 2px var(--color-focus-inner);
  color: var(--color-neutral-10);
}
.has-accessible-features a:focus, .has-accessible-features a:focus:hover {
  text-decoration: none;
}
.has-accessible-features a[disabled] {
  color: var(--color-neutral-6);
}

/*! 2.2. Images */
/* HTML Elements - Images */
img {
  max-width: 100%;
}

figure {
  margin: 0;
}

